import cx from 'classnames';
import FocusTrap from 'focus-trap-react';
import React from 'react';

import { BodyPortal } from '../BodyPortal';
import styles from './styles.module.scss';

export type OverlayProps = {
  children: React.ReactElement<any>;
  className?: string;
  clickOutsideDeactivates?: boolean;
  isOpen?: boolean;
};

export const Overlay: React.FC<OverlayProps> = ({
  className,
  children,
  clickOutsideDeactivates,
  isOpen,
}) => {
  if (!isOpen) {
    return null;
  }

  return (
    <BodyPortal>
      <div className={cx(styles.container, className)}>
        <FocusTrap focusTrapOptions={{ clickOutsideDeactivates }}>
          {children}
        </FocusTrap>
      </div>
    </BodyPortal>
  );
};

export default Overlay;
try {
    // @ts-ignore
    Overlay.displayName = "Overlay";
    // @ts-ignore
    Overlay.__docgenInfo = { "description": "", "displayName": "Overlay", "props": { "className": { "defaultValue": null, "description": "", "name": "className", "required": false, "type": { "name": "string" } }, "clickOutsideDeactivates": { "defaultValue": null, "description": "", "name": "clickOutsideDeactivates", "required": false, "type": { "name": "boolean" } }, "isOpen": { "defaultValue": null, "description": "", "name": "isOpen", "required": false, "type": { "name": "boolean" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["../gamut/src/Overlay/index.tsx#Overlay"] = { docgenInfo: Overlay.__docgenInfo, name: "Overlay", path: "../gamut/src/Overlay/index.tsx#Overlay" };
}
