t;\n  }\n}\n\n@mixin screen-size-gte($min) {\n  @media screen and (min-width: $min) {\n    @content;\n  }\n}\n\n// Named view port mixin\n\n@mixin getViewport($viewport) {\n  @if $viewport == \"lg\" {\n    @include lg-viewport {\n      @content;\n    }\n  }\n  @if $viewport == \"md\" {\n    @include md-viewport {\n      @content;\n    }\n  }\n  @if $viewport == \"sm\" {\n    @include sm-viewport {\n      @content;\n    }\n  }\n  @if $viewport == \"xs\" {\n    @content;\n  }\n}\n"]}]);
